.ask-container {
  //margin-top: 50px;
}

.ask-list-question {
  margin-top: 50px;
}

.ask-nav {
  display: none;
}

.ask-facet {

}

@media only screen and (max-width: 1024px) {

  .ask-nav {
    display: flex;
    width: 100%;
    margin: 25px 0 0 0;
    padding: 0;
    list-style: none;

    $g: &;

    &__item {
      width: 50%;

      &:first-child #{$g}__link {
        border-radius: 25px 0 0 25px;
      }

      &:last-child #{$g}__link {
        border-radius: 0 25px 25px 0;
      }

    }

    &__link {
      display: inline-block;
      padding: 10px 0;
      width: 100%;
      text-align: center;
      font-family: $font-base-book;
      font-weight: normal;
      font-size: 18px;
      color: #fff;
      text-decoration: none;
      background: $color-link;
      border: none;
      outline: 0;
      line-height: 1.15;
      transition: all ease-in-out .1s;

      &.toggled {
        color: $color-description;
        background: #F2F4F8;
      }

    }

  }

  .ask-facet {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    z-index: 101;
    overflow-y: auto;
    transform: translateX(-100%);
    transition: transform .15s cubic-bezier(.4, 0, .2, 1);
    width: 280px;
    background: #fff;

    &.toggled {
      transform: translateX(0);
      transition-duration: 235ms;
    }

  }

  .ask-mask {
    background-color: rgba(0, 0, 0, .5);
    bottom: 0;
    left: 0;
    opacity: 0;
    position: fixed;
    right: 0;
    top: 0;
    transform: translateZ(0);
    transition: opacity .2s linear;
    visibility: hidden;

    &.toggled {
      opacity: 1;
      visibility: visible;
      z-index: 99;
    }

  }

}

/* Small Devices, Tablets */
@media only screen and (max-width: 760px) {

  .ask-container {
    //margin-top: 25px;
  }

  .ask-list-question {
    margin-top: 25px;
  }

}


@media only screen and (max-width: 480px) {

  .ask-nav {

    &__link {
      font-size: 14px;
    }

  }


}
