.record-detail {
  margin-top: 25px;
  display: flex;

  &__col-text {
    flex-grow: 1;
    flex-basis: 0;
    max-width: 100%;
  }

  &__col-img {
    width: 143px;
    min-width: 143px;
    padding-left: 20px;
  }

  &__img {
    width: 100%;
  }

  &__info {
    margin: 10px 0;
    font-family: $font-base-book;
    font-weight: normal;
    font-size: 22px;
    color: $color-description;
    line-height: 1.4;
    opacity: .7;
  }

}

.record-nav {
  display: flex;
  margin: 0;
  padding: 0 0 25px;
  list-style: none;
  border-bottom: 1px solid #e3e7ef;

  &__item {
    margin-right: 30px;

    &:last-child {
      margin-right: 0;
    }

    &_booking {
      display: none;
    }

  }

  &__link {
    padding: 0;
    font-family: $font-base-book;
    font-weight: normal;
    font-size: 22px;
    color: $color-link;
    text-decoration: none;
    background: transparent;
    border: none;
    transition: opacity ease-in-out .2s;

    &:hover {
      opacity: .7;
    }

    & [class^="icon-"], & [class*=" icon-"] {
      position: relative;
      top: 3px;
    }

  }

}

.record {
  font-family: $font-base-book;
  font-weight: normal;
  font-size: 22px;
  color: $color-title;
  line-height: 1.4;

  & .header {
    font-size: 36px;
  }

  & a {
    color: $color-link;
  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width: 1024px) {

  .record-nav {

    &__item {

      &_booking {
        display: inline-block;
      }

    }

  }

  .record-booking {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    z-index: 101;
    overflow-y: auto;
    transform: translateX(-100%);
    transition: transform .15s cubic-bezier(.4, 0, .2, 1);
    width: 280px;
    background: #fff;

    &.toggled {
      transform: translateX(0);
      transition-duration: 235ms;
    }

  }

  .record-mask {
    background-color: rgba(0, 0, 0, .5);
    bottom: 0;
    left: 0;
    opacity: 0;
    position: fixed;
    right: 0;
    top: 0;
    transform: translateZ(0);
    transition: opacity .2s linear;
    visibility: hidden;

    &.toggled {
      opacity: 1;
      visibility: visible;
      z-index: 99;
    }

  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width: 992px) {

  .record-detail {
    flex-direction: column;
  }

  .record-nav {

    &__link {
      font-size: 20px;
    }

  }

}

/* Small Devices, Tablets */
@media only screen and (max-width: 760px) {

  .record-detail {
    flex-direction: column;
    margin-top: 25px;

    &__col-text {

    }

    &__col-img {
      padding-left: 0;
    }

    &__img {

    }

    &__info {
      margin: 5px 0;
      font-size: 18px;
    }

  }

  .record-nav {
    padding: 15px 0;
    flex-wrap: wrap;

    &__item {
      margin-right: 25px;
      margin-bottom: 15px;

      &:last-child {

      }

      &_booking {

      }

    }

    &__link {
      font-size: 18px;
      white-space: nowrap;
    }

  }

  .record {
    font-size: 18px;
    line-height: 1.4;

    & .header {
      display: inline-block;
      position: relative;
      background: transparent;
      font-size: 24px;
      text-align: left;
      z-index: 1;
    }

  }

}

