.alert {
  padding: 20px;
  border-radius: 4px;
  font-family: $font-base-book;
  font-size: 18px;
  color: #004085;
  background-color: #cce5ff;
  border: 1px solid #b8daff;

  & h3,
  & h4 {
    margin: 0 0 5px 0;
    font-size: 24px;
    font-weight: 300;
    color: #334152;
  }

  // Provide class for links that match alerts
  .alert-link {
    color: inherit;
  }

  // Improve alignment and spacing of inner content
  > p,
  > ul {
    font-size: 18px;
    margin-bottom: 0;
    color: #334152;
  }

  > p + p {
    margin-top: 5px;
  }

  & a {
    color: inherit;
  }

}

form .alert {
  margin-bottom: 20px;
}

// Dismissible alerts
//
// Expand the right padding and account for the close button's positioning.

.alert-dismissable, // The misspelled .alert-dismissable was deprecated in 3.2.0.
.alert-dismissible {
  //padding-right: 0;

  // Adjust close link position
  .close {
    display: none;
  }
}

