/* Small Devices, Tablets */
@media only screen and (max-width: 760px) {

  .alert {
    padding: 20px;
    font-size: 16px;

    & h3,
    & h4 {
      font-size: 20px;
    }

    > p,
    > ul {
      font-size: 16px;
    }

  }

}
