.content {

  &__title {
    margin: 0;
    font-family: $font-base-book;
    font-size: 40px !important;
    font-weight: normal;
    color: $color-title;
    line-height: 1.2;
  }

  &__info {
    border-bottom: 1px solid #dde2ec;

    & > span {
      display: block;
      margin: 22px 0;
      font-family: $font-base;
      font-weight: 300;
      font-size: 22px;
      color: $color-description;
      line-height: 1.5;

      & [class^="icon-"], & [class*=" icon-"] {
        position: relative;
        top: 1px;
        font-size: 20px;
        color: #EB3F57;
      }

    }

  }

  &__sub {
    padding-bottom: 25px;
    border-bottom: 1px solid #dde2ec;

    & > span {
      display: inline-block;
      margin-right: 25px;
      font-family: $font-base;
      font-weight: 300;
      font-size: 22px;
      color: $color-description;
      line-height: 1.5;
    }

  }

  &__date {
    display: block;
    margin: 22px 0;
    font-family: $font-base;
    font-weight: 300;
    font-size: 22px;
    color: $color-description;
    line-height: 1.5;

    & [class^="icon-"], & [class*=" icon-"] {
      position: relative;
      top: 1px;
      font-size: 20px;
      color: #EB3F57;
    }

  }

  & h1, & h2, & h3, & h4 {
    font-family: $font-base-book;
    font-weight: normal;
    line-height: 1.2;
  }

  & h1 {
    font-size: 40px;
  }

  & h2 {
    font-size: 36px;
  }

  & h3 {
    font-size: 32px;
  }

  & h4 {
    font-size: 28px;
  }

  & p {
    font-family: $font-base-book;
    font-weight: normal;
    font-size: 22px;
    color: $color-title;
    line-height: 1.4;

    & > img {
      width: 100% !important;
      max-width: 100%;
      height: auto !important;
      border-radius: 5px;
    }

  }

  & a {
    font-family: $font-base-book;
    font-weight: normal;
    font-size: 22px;
    color: $color-link;

    word-wrap: break-word;
    overflow-wrap: break-word;
  }

  & > img {
    width: 100% !important;
    max-width: 100%;
    height: auto !important;
    border-radius: 5px;
  }

  & ul, & ol {
    font-family: $font-base-book;
    font-weight: normal;
    font-size: 22px;
    color: $color-title;
    line-height: 1.4;
  }

  //& .table-responsive {
  //  display: block;
  //  width: 100%;
  //  overflow-x: auto;
  //  -webkit-overflow-scrolling: touch;
  //  -ms-overflow-style: -ms-autohiding-scrollbar;
  //}

  & table {
    display: block;
    width: 100%;
    overflow-x: auto;
    border-collapse: collapse;
    margin: 30px 0;
    max-width: 100%;
    background-color: transparent;
    font-family: $font-base-book;
    font-weight: normal;
    font-size: 22px;
    color: $color-description;
  }

  & table thead th {
    padding: .75rem;
    vertical-align: bottom;
    border-bottom: 2px solid #dee2e6;
  }

  & table td, .table th {
    padding: .75rem;
    vertical-align: top;
    border-top: 1px solid #dee2e6;
  }

  & th {
    text-align: inherit;
  }

}

