.tab-list {
  display: block;
  margin: 25px 0;
  padding: 0;
  list-style: none;

  &:after {
    content: "";
    display: table;
    clear: both;
  }

}

.tab-item {
  //display: inline-block;
  float: left;

  &:first-child .tab-link {
    margin-left: 0;
    border-radius: 50px 0 0 50px;
  }

  &:last-child .tab-link {
    border-radius: 0 50px 50px 0;
  }

}

.tab-item[data-tab-active] {

  & .tab-link {
    color: $color-description;
    background: #f4f7fc;
  }

}

.tab-link {
  display: block;
  margin-left: -1px;
  padding: 10px 35px 16px;
  font-family: $font-base-book;
  font-weight: normal;
  font-size: 22px;
  color: $color-link;
  text-decoration: none;
  border: 1px solid #dde2ec;
  outline: none;
}

.tab-link:focus {

}

.tab-panel {

}

.tab-panel[hidden="true"] {
  background: #c9302c;
  display: none;
}

.tab-panel:focus {
  outline: 0;
}

/* Small Devices, Tablets */
@media only screen and (max-width: 760px) {

  .tab-link {
    padding: 9px 25px 11px;
    font-size: 18px;
  }

}
