.nav-wrap {

}

.nav {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  margin: 0;
  padding: 0;

  $g: &;

  &_main {
    padding: 30px 0 25px 0;

    #{$g}__item {
      margin-top: 10px;
    }

  }

  &_user {
    display: none;
  }

  &_tool {
    display: none;

    #{$g}__item {
      padding-top: 27px;
      border-top: 3px solid transparent;

      &_active {
        border-color: $color-description;

        #{$g}__link {
          opacity: 1;
        }

      }

    }

    #{$g}__link {
      opacity: .6;

      &:hover {
        opacity: 1;
      }

    }

  }

  &_selected-active {

    #{$g}__link {
      opacity: .6;

      &:hover {
        opacity: 1;
      }

      &_active {
        opacity: 1;
      }

    }

  }

  &__item {
    margin-right: 25px;

    &:last-child {
      margin-right: 0;
    }

  }

  &__link {
    font-family: $font-base-book;
    font-size: 22px;
    color: $color-description;
    text-decoration: none;
    transition: opacity ease-in-out .2s;

    &:hover {
      opacity: .5;
    }

    & [class^="icon-"], & [class*=" icon-"] {
      position: relative;
      top: 3px;
    }

  }

}

.nav-hot-wrap {
  padding-top: 70px;
}

.nav-hot-list {
  position: relative;
  bottom: -20px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: -5px;
  padding: 0;
  list-style: none;

  &__item {
    width: 25%;
    padding: 5px;
  }

  &__link {
    position: relative;
    top: 0;
    display: block;
    height: 100%;
    padding: 20px 25px;
    text-decoration: none;
    background: #e74c3c;
    color: #fff;
    border-radius: 5px;
    transition: all ease-in-out .2s;

    &_2 {
      background: #3498db;
    }

    &_3 {
      background: #1abc9c;
    }

    &_4 {
      background: #e67e22;
    }

    &:hover {
      top: -5px;
      box-shadow: 0px 6px 57px 0px rgba(66, 113, 187, 0.21);
    }

  }

  &__title {
    margin: 0;
    font-family: $font-base-book;
    font-weight: normal;
    font-size: 24px;
    color: #fff;
  }

  &__description {
    margin: 15px 0 0 0;
    font-family: $font-base;
    font-weight: 300;
    font-size: 20px;
    color: #fff;
    line-height: 1.2;
  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width: 1200px) {

  .nav-wrap {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    z-index: 99;
    overflow-y: auto;
    transform: translateX(-100%);
    transition: transform .15s cubic-bezier(.4, 0, .2, 1);
    width: 280px;

    background: #fff;
    /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#3f6db8+0,4877c2+100 */
    //background: #3f6db8; /* Old browsers */
    //background: -moz-linear-gradient(left, #3f6db8 0%, #4877c2 100%); /* FF3.6-15 */
    //background: -webkit-linear-gradient(left, #3f6db8 0%, #4877c2 100%); /* Chrome10-25,Safari5.1-6 */
    //background: linear-gradient(to right, #3f6db8 0%, #4877c2 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    //filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#3f6db8', endColorstr='#4877c2', GradientType=1); /* IE6-9 */

    &.toggled {
      transform: translateX(0);
      transition-duration: 235ms;
    }

  }

  .nav-mask {
    background-color: rgba(0, 0, 0, .5);
    bottom: 0;
    left: 0;
    opacity: 0;
    position: fixed;
    right: 0;
    top: 0;
    transform: translateZ(0);
    transition: opacity .2s linear;
    visibility: hidden;
    z-index: 0;

    &.toggled {
      opacity: 1;
      visibility: visible;
      z-index: 4;
    }

  }

  .nav {

    $g: &;

    &_main {
      padding: 20px 25px;

      #{$g}__item {
        width: 100%;
        margin-bottom: 20px;
      }

      #{$g}__link {

      }

    }

    &_user {
      display: flex;
      margin-top: 20px;
      padding: 20px 25px;
      background: rgba(0, 0, 0, .1);

      #{$g}__item {
        margin-right: 25px;

        &:last-child {
          margin-right: 0;
        }

      }

    }

    &_tool {
      display: flex;
      padding: 0 25px;

      #{$g}__item {
        padding-top: 17px;
        margin-right: 25px;

        &:last-child {
          margin-right: 0;
        }

      }

      #{$g}__link {

      }

    }

    &__item {
      margin-right: 0;

      &:last-child {
        margin-right: 0;
      }

    }

    &__link {
      font-size: 22px;
    }

  }

}

/* Medium Devices, Desktops */
@media only screen and (max-width: 992px) {

  .nav-hot-list {

    &__item {
      width: 50%;
    }

    &__link {

    }

    &__title {

    }

    &__description {

    }

  }

}

/* Small Devices, Tablets */
@media only screen and (max-width: 760px) {

  .nav-wrap {
    z-index: 101;
  }

  .nav-mask {

    &.toggled {
      z-index: 100;
    }

  }

  .nav {
    $g: &;

    &_tool {

      #{$g}__item {
        padding-top: 14px;
      }

    }

    &__link {
      font-size: 18px;
    }

  }

  .nav-hot-wrap {
    padding-top: 10px;
  }

  .nav-hot-list {

    &__item {
      width: 100%;
    }

    &__link {
    }

    &__title {
      font-size: 22px;
    }

    &__description {
      margin: 10px 0 0 0;
      font-size: 18px;
    }

  }

}

