.bg-img {
  position: relative;
  background-repeat: no-repeat;
  background-image: url('../images/bg/bg-img-2.jpg');
  background-size: cover;
  background-position: center center;
  width: 100%;

  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,.5);
  }

}

.bg-content {
  position: relative;
  z-index: 1;
}
