.marc-card {
  margin: 25px 0;
  overflow: hidden;
  opacity: 0;
  max-height: 0;
  min-height: auto;
  transition: all ease-in-out .2s;

  &.toggled {
    overflow: auto;
    opacity: 1;
    max-height: 10000px;
  }

}

.marc_dump {
  font-family: $font-base-book;
  font-weight: normal;
  font-size: 20px;
  color: $color-description;
  line-height: 1.8;
}

.marc_dump .fieldlabel, .marc_dump .subfieldlabel {
  color: #c9302c;
}

.marc_dump .fieldlabel, .marc_dump .subfieldlabel, .marc_dump .indicator, .marc_dump .data {

}

.marc_dump .fieldlabel {
  padding-right: 5px;
}

.marc_dump .subfieldlabel {
  padding-left: 5px;
  padding-right: 5px;
}

.marc_dump .innerfield {
  border-left: 1px solid gray;
  padding-left: 5px;
  margin-left: 40px;
}

/* Small Devices, Tablets */
@media only screen and (max-width: 760px) {

  .marc_dump {
    font-size: 16px;
    line-height: 1.4;
  }

}
