.org_tree {
  padding: 0;
  list-style: none;
}

.org_tree__children {
  //list-style: none;
}

.org_tree__children {
  display: none;
  padding: 15px 0 25px 30px;
}

.org_tree__expander {
  display: inline-block;
  width: 19px;
  cursor: pointer;
  margin-right: 5px;
}

.org_tree__expander:after {
  content: "]";
}

.org_tree__expander:before {
  content: "[";
}

.org_tree__expander:hover {
  background: yellow;
  transition: background ease-in-out .2s;
}

.org_tree__org_link {
  font-family: $font-base-book;
  font-weight: normal;
  font-size: 22px;
  color: $color-description;
  text-decoration: none;
  transition: color ease-in-out .2s;

  &:hover {
    color: $color-link;
  }

}

.org_tree__info-link {
  display: none;
  font-family: $font-base-book;
  font-weight: normal;
  font-size: 22px;
  color: $color-link;
  text-decoration: none;

  & [class^="icon-"], & [class*=" icon-"] {
    position: relative;
    top: 3px;
  }

}

.org_tree__node {
  margin-bottom: 10px;

  &:last-child {
    margin-bottom: 0;
  }

}

.org_tree__node:hover > .org_tree__info-link {
  display: inline-block;
}


/* Small Devices, Tablets */
@media only screen and (max-width: 760px) {

  .org_tree__org_link {
    font-size: 16px;
  }

  .org_tree__info-link {
    font-size: 16px;

    & [class^="icon-"], & [class*=" icon-"] {
      position: relative;
      top: 3px;
    }

  }


}
